import MyFriends from './MyFriends';
import { connect } from 'react-redux';
import {addFriendsActionCreator,
        removeFriendsActionCreator,
        avatarClickActionCreator,
        shuffleFriendsActionCreator} from '../../redux/MyFriendsReducer'


let mapStateToProps = (state) => {
    return {
      displaydFriends: state.friendsPage.displaydFriends,
    }
  }

let mapDispatchToProps = (dispatch) => {
    return {
      addFriends: () => {
        dispatch(addFriendsActionCreator());
      },
      removeFriends: () => {
        dispatch(removeFriendsActionCreator());
      },
      avatarClickHandler: (Id) => {
        dispatch(avatarClickActionCreator(Id));
      },
      shuffleFriends: () => {
        dispatch(shuffleFriendsActionCreator());
      }
    }
  }

const MyFriendsContainer = connect(mapStateToProps, mapDispatchToProps) (MyFriends)

export default MyFriendsContainer