import React from 'react';
import s from './Friend.module.css';

const Friend = (props) => {
    return (
        <div onClick={props.onAvatarClick} className={s.friend} id={props.id}>
            <p>{props.name}</p>
            <img src={props.imgSrc} alt=""/>
            <p></p>
        </div>
    )
}

export default Friend