import React from 'react';
import s from './MyFriends.module.css';
import Friend from './Friend/Friend';


const MyFriends = (props) => {

    let onAddFriends = () => {
        props.addFriends();
    }
    let onRemoveFriends = () => {
        props.removeFriends();
    }
    let onAvatarClick = (e) => {
        let Id = e.currentTarget.id
        props.avatarClickHandler(Id);
    }
    let onShuffleFriends = () => {
        props.shuffleFriends();
    }


    let friendsComponents = props.displaydFriends.map( (f, index) =>
    <Friend key={index} id={f.id} name={f.name} imgSrc={f.imgSrc} onAvatarClick={onAvatarClick}/>)
    return (
        <div>
            <button onClick={onAddFriends}>add</button>
            <button onClick={onRemoveFriends}>delete</button>
            <button onClick={onShuffleFriends}>shuffle</button>
            <div className={s.myFriends}>
                {friendsComponents}

            </div>
        </div>
    )
}

export default MyFriends