import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
  return (

    <div className={s.item}>
      <img src={props.imgSrc} />
      My name is: {props.name}
      <div>
        <span>like: {props.likeCount}</span>
        <br />
        <span>{props.message}</span>
      </div>
    </div>


  );
}

export default Post;