const ADD_POST = 'ADD-POST';
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';

let initialState = {
    posts: [
        { id: 1, message: 'How are you', likeCount: '5', imgSrc: 'https://cdn.fishki.net/upload/post/2017/03/19/2245758/tn/02-funny-cat-wallpapercat-wallpaper.jpg', name: 'cat' },
        { id: 2, message: 'My first posts', likeCount: '15', imgSrc: 'https://www.ejin.ru/wp-content/uploads/2017/12/2-3-696x392.jpg', name: 'caticat' },
        { id: 3, message: 'My first', likeCount: '25', imgSrc: 'http://pikstok.ru/images/images/1528372927887.jpg', name: 'vasya' },
    ],
    profileAvatar: [
        { avaSrc: "http://fentezi-mir.ru/images/1/angel_326.jpg" }
    ],
    newPostText: 'it-kamasutra',
    profile: null
}

const profileReducer = (state = initialState, action) => {
    switch (true) {
        case (action.type === ADD_POST): {
            let newPost = {
                id: (state.posts.length + 1),
                message: state.newPostText,
                likeCount: 1,
                imgSrc: 'http://mkuniverse.at.ua/_si/7/95262110.png',
                name: 'rain'
            }
            return {
                ...state,
                posts: [...state.posts, newPost],
                newPostText: ''
            };
        }
        case (action.type === UPDATE_NEW_POST_TEXT): {
            return {
                ...state,
                newPostText: action.newText
            };
        }
        case (action.type === SET_USER_PROFILE): {
            return {
                ...state,
                profile: action.profile}
            }
        default: 
            return state;
    }
    return state;
}

export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const addPostActionCreator = () => ({ type: ADD_POST })
export const updateNewPostTextActionCreator = (text) =>
    ({ type: UPDATE_NEW_POST_TEXT, newText: text })

export default profileReducer