const ADD_NEW_MESSAGE = 'ADD-NEW-MESSAGE';
const REMOVE_MESSAGE = 'REMOVE-MESSAGE';
const UPDATE_MESSAGE = 'UPDATE-MESSAGE';

let initialState = {
    dialogs: [
        { id: 1, name: 'Dimich' },
        { id: 2, name: 'Andrew' },
        { id: 3, name: 'Sveta' },
        { id: 4, name: 'Sasha' },
        { id: 5, name: 'Valera' }
    ],
    messages: [
        { id: 1, message: 'hi' },
        { id: 2, message: 'hi hi hi' },
        { id: 3, message: 'hi for all' },
        { id: 4, message: 'hello' },
        { id: 5, message: 'hello hello' }
    ],
    newMessageText: 'mesage mesage',
    buttonLabels: [
        { nameButton: 'add Message' },
        { nameButton: 'remove Message' }
    ]

}

export const dialogsReducer = (state = initialState, action) => {
    switch (true) {
        case (action.type === ADD_NEW_MESSAGE): {
            let text = state.newMessageText;
            return {
                ...state, newMessageText: '',
                messages: [...state.messages, {id: (state.messages.length + 1), message: text}]
            }
        }
        case (action.type === REMOVE_MESSAGE): {
            let stateCopy = {...state};
            stateCopy.messages.pop();
            return stateCopy;
        }
            
        case (action.type === UPDATE_MESSAGE): {
            return {
                ...state, 
                newMessageText: '',
                newMessageText: action.newText
            }
        }

        default:
            return state;
    }
    return state;
}

export const addNewMessageActionCreator = () => ({ type: ADD_NEW_MESSAGE });
export const onMessageChangeActionCreator = (text) =>
    ({ type: UPDATE_MESSAGE, newText: text });
export const deleteMessageActionCreator = () => ({ type: REMOVE_MESSAGE });

export default dialogsReducer