import _ from 'lodash';

const ADD_FRIENDS = 'ADD_FRIENDS';
const REMOVE_FRIENDS = 'REMOVE_FRIENDS';
const AVATAR_CLICK = 'AVATAR_CLICK';
const SHUFFLE_FRIENDS = 'SHUFFLE_FRIENDS';


let initialState = {
    allFriends: [
        { id: 0, name: 'rain', imgSrc: 'http://mkuniverse.at.ua/_si/7/95262110.png' },
        { id: 1, name: 'noob', imgSrc: 'https://aa1a5178aef33568e9c4-a77ea51e8d8892c1eb8348eb6b3663f6.ssl.cf5.rackcdn.com/p/full/f8e06fc3-0e07-4d79-bd24-547133706b8a.jpg' },
        { id: 2, name: 'cyrax', imgSrc: 'https://t7.rbxcdn.com/af3970d794e8c2dec71274153d2a8e88' },
        { id: 3, name: 'sector', imgSrc: 'https://vignette.wikia.nocookie.net/mortalkombat/images/3/35/Sektbio.gif/revision/latest?cb=20081218053748&path-prefix=es' },
        { id: 4, name: 'liu', imgSrc: 'https://static.giantbomb.com/uploads/original/0/3592/261966-liukang_render.jpg' },
        { id: 5, name: 'sindel', imgSrc: 'http://images6.fanpop.com/image/photos/37400000/Sindel-mortal-kombat-37492226-840-1120.jpg' },
        { id: 6, name: 'smoke', imgSrc: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSY523tBC2x3xQdPsM3M1epfWNGbARnqFXOgOeByI1yIz9XxqDXPA' },
        { id: 7, name: 'subzero', imgSrc: 'https://i.ytimg.com/vi/SNP0EEaCvUA/maxresdefault.jpg' },
        { id: 8, name: 'scorpion', imgSrc: 'https://static3.srcdn.com/wordpress/wp-content/uploads/2016/11/Scorpion-holding-spear-in-Mortal-Kombat-X.jpg' },
    ],
    displaydFriends: [
        { id: 7, name: 'subzero', imgSrc: 'https://i.ytimg.com/vi/SNP0EEaCvUA/maxresdefault.jpg' },
        { id: 2, name: 'cyrax', imgSrc: 'https://t7.rbxcdn.com/af3970d794e8c2dec71274153d2a8e88' }

    ]
}

const myFriendsReducer = (state = initialState, action) => {

    switch (action.type) {

        case ADD_FRIENDS:

            let differenceBetweenFriend = _.differenceBy(state.allFriends, state.displaydFriends, 'id')
            let fourFriend = differenceBetweenFriend.slice(0, 2)

            return {
                ...state,
                displaydFriends: [...state.displaydFriends.concat(fourFriend)]
            }

        case REMOVE_FRIENDS: {

            let stateCopy = { ...state }
            stateCopy.displaydFriends = [...state.displaydFriends];
            stateCopy.displaydFriends.pop();

            return {
                ...stateCopy
            }
        }
        case AVATAR_CLICK: {
            let stateCopy = { ...state }
            stateCopy.displaydFriends = [...state.displaydFriends];
            stateCopy.displaydFriends = stateCopy.displaydFriends.filter(f => f.id != action.Id)

            return {
                ...stateCopy
            }
        }
        case SHUFFLE_FRIENDS: {
            let stateCopy = { ...state }
            stateCopy.displaydFriends = [...state.displaydFriends];
            console.log('gg')
        }

        default:
            return state;
    }

}

export const addFriendsActionCreator = () => ({ type: ADD_FRIENDS })
export const removeFriendsActionCreator = () => ({ type: REMOVE_FRIENDS })
export const avatarClickActionCreator = (Id) => ({ type: AVATAR_CLICK, Id: Id })
export const shuffleFriendsActionCreator = () => ({ type: SHUFFLE_FRIENDS })


export default myFriendsReducer