import React from 'react';
import s from './Navbar.module.css';
import {NavLink} from 'react-router-dom'

const Navbar = () => {
  return (
    <nav className={s.nav}>
      <div className={`${s.item} ${s.active}`}>
        <NavLink to="/profile" activeClassName={s.activeLink} alt="">Profile</NavLink>
      </div>
      <div className={s.item}>
        <NavLink to="/dialogs" activeClassName={s.activeLink} alt="">Messages</NavLink>
      </div>
      <div className={s.item}>
        <NavLink to="/users" activeClassName={s.activeLink} alt="">Users</NavLink>
      </div>
      <div className={s.item}>
        <NavLink to="/news" activeClassName={s.activeLink} alt="">News</NavLink>
      </div>
      <div className={s.item}>
        <NavLink to="/music" activeClassName={s.activeLink} alt="">Music</NavLink>
      </div>
      <div className={s.item}>
        <NavLink to="/settings" activeClassName={s.activeLink} alt="">Settings</NavLink>
      </div>
      <div className={s.item}>
        <NavLink to="/myFriends" activeClassName={s.activeLink} alt="">My friends</NavLink>
      </div>
    </nav>
  )
}

export default Navbar;